import React from "react";
import "../../App.css";
import "bootstrap/dist/css/bootstrap.min.css";

import Col from "react-bootstrap/Col";
import Dropdown from "react-bootstrap/Dropdown";
import DropdownButton from "react-bootstrap/DropdownButton";

import Environment from "./Environment";

import { CSSTransition } from "react-transition-group";

export default function Portfolio() {
  const [current, setCurrent] = React.useState({
    name: "Nature",
    comp: <Environment />,
  });
  const [inProp, setInProp] = React.useState(true);

  const handleSelect = (e) => {
    if (e === "1") {
      setCurrent({
        name: "Nature",
        comp: <Environment />,
      });
      setInProp(false);
    }
  };

  return (
    <Col className="p-3 work-sans-300 ">
      <h1 className="primary work-sans-200"> UX Portfolio </h1>
      <hr className="hr" />
      <DropdownButton title={current.name} className="dropdown mb-4">
        <Dropdown.Item
          eventKey="1"
          onSelect={(eventKey) => handleSelect(eventKey)}
        >
          Nature
        </Dropdown.Item>
      </DropdownButton>
      <p className="work-sans-400 primary">
        Select a design above and click on the below images to enlarge them.
      </p>
      <br />
      <CSSTransition
        in={inProp}
        classNames="item"
        onExiting={() => setInProp(true)}
        timeout={3000}
      >
        {current.comp}
      </CSSTransition>
    </Col>
  );
}
