import React from "react";
import "../../App.css";
import "bootstrap/dist/css/bootstrap.min.css";

/* Importing Gallery Images */
import img1 from "../../img/environment/EN1.png";
import img2 from "../../img/environment/EN2.png";
import img3 from "../../img/environment/EN3.png";
import img4 from "../../img/environment/EN4.png";

import Carousel from "react-bootstrap/Carousel";
import ModalImage from "react-modal-image";

export default function Environment() {
  return (
    <div>
      <Carousel indicators={false} interval={null}>
        <Carousel.Item>
          <ModalImage
            hideDownload
            hideZoom
            className="gallery-img"
            small={img1}
            large={img1}
          />
        </Carousel.Item>
        <Carousel.Item>
          <ModalImage
            hideDownload
            hideZoom
            className="gallery-img"
            small={img2}
            large={img2}
          />
        </Carousel.Item>
        <Carousel.Item>
          <ModalImage
            hideDownload
            hideZoom
            className="gallery-img"
            small={img3}
            large={img3}
          />
        </Carousel.Item>
        <Carousel.Item>
          <ModalImage
            hideDownload
            hideZoom
            className="gallery-img"
            small={img4}
            large={img4}
          />
        </Carousel.Item>
      </Carousel>
      <br />
      <p className="work-sans-400 primary">
        A simple theme designed for environmental organisations.
      </p>
    </div>
  );
}
